/**
 * Fonts
 * -----
 *
 */
// import * as responsive from './responsive';
import { Responsive } from './responsive';
// Fonts ------------------------------------------------------------
export var defaultFont = "font-family: 'Roboto', -apple-system, BlinkMacSystemFont, 'Arial','Segoe UI', Helvetica, Arial,sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';";
// Weights ----------------------------------------------------------
// const thin = 'font-weight: 200;'
var light = 'font-weight: 300;';
var regular = 'font-weight: 400;';
var bold = 'font-weight: 600;';
var rem = function (size) { return size / 10 + "rem"; };
// Size -------------------------------------------------------------
export var FontWeight = {
    LIGHT: 300,
    REGULAR: 400,
    BOLD: 600,
};
var fontSize = function (size) { return "\n  font-size: " + size + "px;\n  font-size: " + rem(size) + ";\n"; };
var responsiveStyle = function (sizes, weight) {
    var size = sizes.base;
    if (!size)
        throw new Error('Responsive Style - you need to provide a base value');
    var responsiveSizes = [];
    Object.keys(sizes).forEach(function (key) {
        if (key !== 'base') {
            responsiveSizes.push({
                breakpoint: function (content) { return "@media " + Responsive[key] + " { " + content + "}"; },
                size: sizes[key],
            });
        }
    });
    return "\n        " + defaultFont + "\n        " + fontSize(size) + "\n        " + weight + "\n        " + responsiveSizes.map(function (style) { return (style ? style.breakpoint(fontSize(style.size)) : ''); }) + "\n    ";
};
/**
 * Font
 */
var Font = function (size, weight, lineHeight, margin) { return "\n  " + defaultFont + "\n  " + fontSize(size) + "\n  " + weight + "\n  " + (lineHeight ? "line-height:" + rem(lineHeight) + ";" : '') + "\n  " + (margin ? "margin:" + rem(margin) + " 0;" : '') + "\n"; };
export var h1 = Font(36, light, 40, 20);
export var h2 = Font(28, light, 32, 15);
export var h3 = Font(22, light, 25, 10);
export var h4 = Font(18, light, 21, 8);
export var h5 = Font(16, regular, 19, 6);
export var h6 = Font(14, bold, 16, 4);
export var font1 = Font(16, regular);
export var font2 = Font(14, light);
export var font3 = Font(14, regular);
export var font4 = Font(12, regular);
export var font5 = Font(11, regular);
export var h1Responsive = responsiveStyle({ base: 20, DESK_BP: 28 }, light);
export var h2Responsive = responsiveStyle({ base: 18, DESK_BP: 24 }, regular);
export var h3Responsive = responsiveStyle({ base: 18, DESK_BP: 22, MOBILE_BP: 20 }, light);
export var h4Responsive = responsiveStyle({ base: 16, DESK_BP: 20 }, light);
