/** common responsive breakpoint rules */
/*
  ##Device = Most of the Smartphones Mobiles (Portrait)
  ##Screen = B/w 320px to 479px
*/
var MOBILE_BP = 'screen and (min-width: 320px) and (max-width: 480px)';
/*
  ##Device = Low Resolution Tablets, Mobiles (Landscape)
  ##Screen = B/w 481px to 767px
*/
var MOBILE_LANDSCAPE_BP = 'screen and (min-width: 481px) and (max-width: 767px)';
/*
  ##Device = Tablets, Ipads (portrait)
  ##Screen = B/w 768px to 1024px
*/
var TABLET_BP = 'screen and (min-width: 768px) and (max-width: 1024px)';
/*
  ##Device = Tablets, Ipads (landscape)
  ##Screen = B/w 768px to 1024px
*/
var TABLET_LANDSCAPE_BP = 'screen and (min-width: 768px) and (max-width: 1024px) and (orientation: landscape)';
/*
  ##Device = Laptops, Desktops
  ##Screen = B/w 1025px to 1280px
*/
var DESK_BP = 'screen and (min-width: 1025px) and (max-width: 1280px)';
/*
  ##Device = Desktops
  ##Screen = 1281px to higher resolution desktops
*/
var DESK_WIDE_BP = 'screen and (min-width: 1281px)';
export var Responsive = {
    MOBILE_BP: MOBILE_BP,
    MOBILE_LANDSCAPE_BP: MOBILE_LANDSCAPE_BP,
    TABLET_BP: TABLET_BP,
    TABLET_LANDSCAPE_BP: TABLET_LANDSCAPE_BP,
    DESK_BP: DESK_BP,
    DESK_WIDE_BP: DESK_WIDE_BP,
};
/*
  use these constants to perform actions based on device side
*/
export var isMobile = function () { return window.matchMedia(MOBILE_BP).matches; };
export var isMobileLandscape = function () { return window.matchMedia(MOBILE_LANDSCAPE_BP).matches; };
export var isTablet = function () { return window.matchMedia(TABLET_BP).matches; };
export var isTabletLandscape = function () { return window.matchMedia(TABLET_LANDSCAPE_BP).matches; };
export var isDesk = function () { return window.matchMedia(DESK_BP).matches; };
export var isDeskWide = function () { return window.matchMedia(DESK_WIDE_BP).matches; };
