/** font base size */
export var baseFontSize = 16;
export var baseUnit = 4;
/** spacing calculation */
export var spacing = function (multiplier) {
    if (multiplier === void 0) { multiplier = 0; }
    return (baseUnit * multiplier) / baseFontSize + "rem";
};
/** hex to rgb converter */
export var hexToRgbaString = function (hexColor, alpha) {
    var result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hexColor);
    return result
        ? "rgba(" + parseInt(result[1], 16) + "," + parseInt(result[2], 16) + "," + parseInt(result[3], 16) + "," + alpha + ")"
        : hexColor;
};
